<p>Copyright law protects ‘literary and artistic works’, a broad expression which
  includes a wide range of creative output such as books, paintings, music, film,
  plays, computer code, photographs, architecture, illustrations, and sculptures,
  among others. ‘Derivative works’ such as translations and adaptations of existing
  works are also protected. In the European Union, works are protected by
  copyright if they can be considered ‘original’. The Court of Justice of the European
  Enforcement
  Union has established that a work is original if it is the ‘author’s own intellectual
  creation’. This means that the work must involve a sufficient level of creativity to
  reflect the author’s personality. More ‘entrepreneurial’ types of work such as
  sound recordings or broadcasts are protected by ‘related rights’ (LINK), and so are
  performances. Databases can also be protected either by copyright or by weaker
  ‘sui generis’ rights. The law of individual EU member states may present some
  differences regarding the types of works protected by copyright. Technically, the
  works protected by copyright are referred to as ‘subject-matter’.
  </p>

